import index from '../page/index'
import login from '../page/login/login'
import notFound from '../components/notFound'

export default [
    {
        path:'/',
        name:'Index',
        component:index,
        auth:true
    },
    {
        path:'/login',
        name:'login',
        component:login
    },
    {
        path:'/notFound',
        name:'notFound',
        component:notFound
    }
]