import React, {Component} from 'react';
import Header from '../../components/header'
import {Row, Col, Select, Form, Input, Button, Icon, Tooltip} from 'antd'
import {Link } from "react-router-dom"
import {login} from "../../api/user"
import propTypes from 'prop-types'
import cookie from 'react-cookies'
const {Option} = Select;

class UserProxy extends Component {
    render() {
        return (
            <div className='marBom-10'>
                登录即代表同意<a className='untill-hover' href="">《用户服务协议》</a>和<a
                className='untill-hover'
                href="">《隐私政策》</a>
            </div>
        )
    }
}

class mobileForm extends Component {
    handleSubmit(e) {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                console.log('Received values of form: ', values);
            }
        });
    };

    render() {
        const {getFieldDecorator} = this.props.form;
        const prefixSelector = getFieldDecorator('prefix', {
            initialValue: '86',
        })(
            <Select style={{width: 70}}>
                <Option value="86">+86</Option>
                <Option value="87">+87</Option>
            </Select>,
        );
        return (
            <Form className='login-form' onSubmit={this.handleSubmit.bind(this)}>
                <Form.Item>
                    {getFieldDecorator('phone', {
                        rules: [{required: true, message: '请输入手机号！'}],
                    })(<Input placeholder="手机号" addonBefore={prefixSelector} style={{width: '100%'}}/>)}
                </Form.Item>
                <Form.Item>
                    {getFieldDecorator('captcha', {
                        rules: [{required: true, message: '请输入验证码！'}],
                    })(<Input placeholder="动态密码" style={{width: 'calc(100% - 10rem)'}}/>)}
                    <Button style={{float: 'right'}} className='color'>发送动态密码</Button>
                </Form.Item>
                <Form.Item>
                    <Button type="primary" htmlType="submit" className="login-form-button">
                        登录
                    </Button>
                </Form.Item>
            </Form>
        )
    }
}

class accountForm extends Component {
    handleSubmit(e) {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                login(values).then((res)=>{
                    if(res.code === 0){
                        cookie.save('user',values.username)
                        this.props.history.push('/')
                    }
                })
            }
        });
    };
    render() {
        const {getFieldDecorator} = this.props.form;
        return (
            <Form className='login-form' onSubmit={this.handleSubmit.bind(this)}>
                <Form.Item>
                    {getFieldDecorator('username', {
                        rules: [{required: true, message: '请输入账户！'}],
                    })(
                        <Input
                            prefix={<Icon type="user" style={{color: 'rgba(0,0,0,.25)'}}/>}
                            placeholder="手机/邮箱/用户名"
                        />,
                    )}
                </Form.Item>
                <Form.Item>
                    {getFieldDecorator('password', {
                        rules: [{required: true, message: '请输入密码！'}],
                    })(
                        <Input
                            prefix={<Icon type="lock" style={{color: 'rgba(0,0,0,.25)'}}/>}
                            type="password"
                            placeholder="密码"
                        />,
                    )}
                </Form.Item>
                <Form.Item>
                    <a className={`login-form-forgot hover`} href="">忘记密码</a>
                    <Button type="primary" htmlType="submit" className="login-form-button">
                        登录
                    </Button>
                </Form.Item>
            </Form>
        )
    }
}
accountForm.propTypes = {
    history: propTypes.object.isRequired
}
let MobileForm = Form.create({name: 'mobile'})(mobileForm);
let AccountForm = Form.create({name: 'account'})(accountForm);

export default class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isLogin:false,
            tabName: 1,
            leftImg: require('../../assets/img/login/leftimg.png'),
            mobileType: false,
            codeType: false,
            timeEnd: false,
        };
    }

    // tab切换
    changeTab(key) {
        if(key === 1 && this.state.codeType){
            this.refreshCode(false)
        }
        this.setState({
            tabName: key
        })
    }

    // 手机验证码登录
    changeMobileType() {
        this.setState({
            mobileType: !this.state.mobileType
        })
    }

    //二维码登录
    changeCodeType() {
        if (!this.state.codeType) {
            setTimeout(() => {
                this.refreshCode(true)
            }, 3 * 1000)
        } else {
           this.refreshCode(false)
        }
        this.setState({
            codeType: !this.state.codeType
        })
    }

    // 刷新二维码登录
    refreshCode(key) {
        this.setState({
            timeEnd: key
        })
    }

    render() {
        return (
            <div className='yl-login'>
                <Header/>
                <Row style={{paddingTop: '20rem'}}>
                    <Col md={{span: 22, offset: 1}} lg={{span: 18, offset: 3}}>
                        <div className="yl-login-contain">
                            {/* 左侧图片 */}
                            <div className='yl-login-left'>
                                <img src={this.state.leftImg} alt='login'/>
                            </div>
                            {/* 右侧登录框 */}
                            <div className='yl-login-wrap'>
                                {/* 登录tab标签 */}
                                <div className='yl-login-tab-head'>
                                    <span onClick={this.changeTab.bind(this, 1)}
                                          className={`${this.state.tabName === 1 ? "active" : ""}`}>账号登录</span>
                                    <span onClick={this.changeTab.bind(this, 2)}
                                          className={`${this.state.tabName === 2 ? "active" : ""}`}>QQ登录</span>
                                    <span onClick={this.changeTab.bind(this, 3)}
                                          className={`${this.state.tabName === 3 ? "active" : ""}`}>微信登录</span>
                                </div>
                                {/* 账号登录 */}
                                <div
                                    className={`yl-login-tab yl-login-form ${this.state.tabName === 1 ? 'show' : 'hide'}`}>
                                    <div className={`${!this.state.codeType ? "show" : "hide"}`}>
                                        {/*账号登录*/}
                                        <div className={`${!this.state.mobileType ? "show" : "hide"}`}>
                                            <AccountForm history={this.props.history}/>
                                        </div>
                                        {/*手机号码登录*/}
                                        <div className={`${this.state.mobileType ? "show" : "hide"}`}>
                                            <MobileForm/>
                                        </div>
                                        <div
                                            className={`yl-login-code tc ${!this.state.codeType ? "show" : "hide"}`}>
                                            <div className='marBom-20'>
                                                <span className='hover' onClick={this.changeMobileType.bind(this)}>
                                                           {this.state.mobileType ? '账号密码登录' : '手机验证码登录'}</span>&nbsp;&nbsp;|&nbsp;&nbsp;
                                                <Link className='hover' to='/regist'>免费注册</Link>
                                            </div>
                                            <UserProxy/>
                                            <div className='other-login'>
                                                <Tooltip title="功能暂未开启，敬请期待">
                                                    <i className='iconfont icon-zhifubao' title='支付宝登录'/>
                                                </Tooltip>
                                                <Tooltip title="功能暂未开启，敬请期待">
                                                    <i className='iconfont icon-tubiao214' title='新浪登录'/>
                                                </Tooltip>
                                                <Tooltip title="功能暂未开启，敬请期待">
                                                    <i className='iconfont icon-baidu1' title='百度登录'/>
                                                </Tooltip>
                                            </div>
                                        </div>
                                    </div>
                                    {/* 二维码登录 */}
                                    <div className={`yl-login-code tc ${this.state.codeType ? "show" : "hide"}`}>
                                        <span style={{fontSize: '1.6rem'}}>扫码登录</span>
                                        <div className={`yl-login-code-model tc`}>
                                            <img src={require('../../assets/img/login/code.jpg')} alt=''/>
                                            <div onClick={this.refreshCode.bind(this)}
                                                 className={`${this.state.timeEnd ? 'bg-model' : 'hide'}`}>
                                                <i className='iconfont icon-gantanhao'/>
                                                <p>二维码已过期</p>
                                                <p>请点击刷新</p>
                                            </div>
                                        </div>
                                        <p>打开<span className='untill-hover'>手机网页端渔火写作</span></p>
                                        <p>扫一扫登录</p>
                                        <div className='yl-login-code-icon yl-flex-space-around'>
                                            <div><i className='iconfont icon-jianpan'/><span>免输入</span></div>
                                            <div><i className='iconfont icon-anquan'/><span>更安全</span></div>
                                            <div><i className='iconfont icon-shandian-shi'/><span>更快捷</span></div>
                                        </div>
                                    </div>
                                    <div className='codeSwitch'>
                                        <img onClick={this.changeCodeType.bind(this)}
                                             className={`${this.state.codeType ? "show" : "hide"}`}
                                             src={require('../../assets/img/login/computed.png')} alt=''/>
                                        <img onClick={this.changeCodeType.bind(this)}
                                             className={`${!this.state.codeType ? "show" : "hide"}`}
                                             src={require('../../assets/img/login/code2.png')} alt=''/>
                                    </div>
                                </div>
                                {/* qq 登录 */}
                                <div className={`yl-login-tab tc ${this.state.tabName === 2 ? 'show' : 'hide'}`}>
                                    <div className={`yl-login-code-model`}>
                                        <img className={'marBom-20 '}
                                             src={`${require('../../assets/img/login/qq.png')}`} alt={'qq'}/>
                                    </div>
                                    <p className={'marBom-10'}
                                       style={{fontSize: '1.5rem', color: '#aaa'}}>您将跳转至指定页面进行登录操作</p>
                                    <Button type="primary" className="login-form-button marBom-10">
                                        前去QQ登录
                                    </Button>
                                    <UserProxy/>
                                </div>
                                {/* 微信登录 */}
                                <div className={`yl-login-tab tc ${this.state.tabName === 3 ? 'show' : 'hide'}`}>
                                    <div className={`yl-login-code-model tc`}>
                                        <img className={'marBom-10 '}
                                             style={{border: '1px solid #ddd', width: '19rem', height: '19rem'}}
                                             src={require('../../assets/img/login/code.jpg')} alt=''/>
                                    </div>
                                    <p>请使用微信扫描二维码登录</p>
                                    <p className={'marBom-10'}>“渔火写作”</p>
                                    <UserProxy/>
                                </div>
                            </div>
                        </div>
                    </Col>
                </Row>
            </div>
        )
    }
}