import React, { Component } from 'react';
import Template from '../components/template'

export default class Home extends Component {
    constructor(props){
        super(props)
        this.state = {
            message:'你娘'
        }
        this.tick = this.tick.bind(this);
    }
    tick(){
        this.setState({
            message:'我是你爹'
        })
    }
    render() {
        return (
            <Template>
                hhaha
            </Template>
        )
    }
}