import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import { Row,Col,Icon ,Badge} from 'antd'

export default class Header extends Component {
    constructor(props){
        super(props);
        this.state = {
            webSiteName:'渔火',
            webSiteDes:'我的地盘'
        };
    }
    componentDidMount() {
    }

    componentWillUnmount() {

    }
    tick(){
        this.setState({
            date:new Date()
        })
    }
    render() {
        return (
            <div className='yl-header'>
                <Row style={{height:'100%'}}>
                    <Col md={{ span: 22,offset:1 }} lg={{ span: 18,offset:3 }}>
                        <Col span={12} className='yl-header-left'>
                            <span>{this.state.webSiteName}</span>
                            <span>·</span>
                            <span>{this.state.webSiteDes}</span>
                        </Col>
                        <Col span={12} className='yl-header-right tr'>
                            <Badge count={5}>
                                <Icon type="bell" />
                            </Badge>
                            <Icon type="user" />
                        </Col>
                    </Col>
                </Row>
            </div>
        )
    }
}