import React, { Component } from 'react';

export default class Header extends Component {
    constructor(props){
        super(props);
        this.state = {
            date: new Date(),
            name:'yangrena'
        };
    }
    componentDidMount() {
        this.timerID = setInterval(()=>{
            this.tick()
        },1000)
    }

    componentWillUnmount() {
        clearInterval(this.timerID)
    }
    tick(){
        this.setState({
            date:new Date()
        })
    }
    render() {
        return (
            <div className='yl-header'>
                这是404
            </div>
        )
    }
}