import React, {Component} from 'react';
import Header from "../components/header"
import Side from "../components/side"

export default class Template extends Component {
    constructor(props) {
        super(props);
        this.state = {};
    }
    render() {
        return (
            <div style={{height:'100%'}}>
                <Header/>
                <Side/>
                <div className='yl-contain'>
                    {this.props.children}
                </div>
            </div>
        )
    }
}